import { Component, ViewChild }   from '@angular/core';
import { MODAL_DIRECTIVES, ModalComponent } from 'ng2-bs3-modal/ng2-bs3-modal';

@Component({
  selector: 'parent-component',
  directives: [],
  template: `
        <modal #myModal>
            ...
        </modal>
    `
})
export class mYModalComponent {
  @ViewChild('myModal')
      modal: ModalComponent;

  close() {
    this.modal.close();
  }

  open() {
    this.modal.open();
  }
}